import React, { useState,useEffect } from "react";
import axios from "axios";
import {message, Form, Input, Button, Alert} from 'antd';
import { Link, useNavigate } from "react-router-dom";
import TextArea from "antd/es/input/TextArea";
import AdminHeader from './AdminHeader';
import AdminFooter from './AdminFooter';
export const AdminAddclient = () => {
  const navigate = useNavigate();
  const [data, setData] = useState([]);
  useEffect(() => {
    document.title = 'Add New Client';
    const userStr = JSON.parse(localStorage.getItem("admin"));
    if (userStr == null) {
      navigate('/admin/login');
    }
    axios.get("http://localhost:8000/admin/getclient",{headers:{'Access-Control-Allow-Origin':'*','Access-Control-Allow-Methods': 'POST,PUT','Accept': 'application/json','Content-Type':'application/json'}})
    .then(res => { 
      console.log(res.data.results);
      setData(res.data.results);
      
    }) 
    .catch(err => { 
      
    })
}, []);

const [value,setValue]=useState('');
const handleInputChange = (e) => {
  const inputValue = e.target.value;
  
  // Validate input as integer
  if (/^\d*$/.test(inputValue)) {
    setValue(inputValue);
  }
};


const [showAlert, setShowAlert] = useState(false)
const handleSubmit = (event) => {
  event.preventDefault();
  var vll = event.target;
  let formdata = {
    email: vll.email.value,
    name: vll.name.value,
    location: vll.location.value,
    position: vll.position.value,
    department: vll.department.value,
    phone_number: vll.phone_number.value,
    mobile_number: vll.mobile_number.value,
    home_phone_number: vll.home_phone_number.value,
    fax_number: vll.fax_number.value,
  };
  axios.post("http://localhost:8000/admin/addclient", formdata,{headers:{'Access-Control-Allow-Origin':'*','Access-Control-Allow-Methods': 'POST,PUT','Accept': 'application/json','Content-Type':'application/json'}})
    .then(res => { 
     // console.log(res.data);
      if(res.data.status == '2'){
        setShowAlert(true);
        setShowAlert({ type: 'error' });
        
      } else {
        axios.get("http://localhost:8000/admin/getclient",{headers:{'Access-Control-Allow-Origin':'*','Access-Control-Allow-Methods': 'POST,PUT','Accept': 'application/json','Content-Type':'application/json'}})
        .then(res => { 
          console.log(res.data.results);
          setData(res.data.results);
          
        }) 
        .catch(err => { 
          
        })
        setShowAlert(true);
        setShowAlert({ type: 'success' });
        event.target.reset();
        //(event.target).text()
      }
      setTimeout(() => {
        setShowAlert(false);
      }, 4500);
    }) 
    .catch(err => { 
      
    })
}

  
  return (
    <>
    <div>
    <AdminHeader />
    <div className="main-right-body">
      <div className="block-full">
        <div className="row gy-4">
          <div className="col-md-4">
            <div className="d-block red-heading bg-white shadow p-2">
              <h3 className="mb-3">Clients</h3>
              <div className="over-empl">
                <div className="d-flex flex-column h-100">               
                  {Array.isArray(data) ? (
                  data.map((item, index) => <Link to="" >{item.name}</Link>)
                ) : (
                  <a href="#"> No data available </a>
                )}
                </div>
              </div>
            </div>
          </div>
          <div className="col-md-8">
            {
              showAlert?.type === 'error'&&<Alert type="error"  description="Email is already exist" closable/>
            }
            {
              showAlert?.type === 'success'&&<Alert type="success"  description="Client has been successfully created!" closable/>
            }
            <div className="right-form-box bg-white shadow p-2">
              <form action="" onSubmit={handleSubmit}>
                <div className="row gy-2">
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Client Name <span className="text-danger fs-14">*</span></label>
                      <input type="text" required name="name" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Client Location <span className="text-danger fs-14">*</span></label>
                      <input type="text" required name="location" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Position <span className="text-danger fs-14">*</span></label>
                      <input type="text" required name="position" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Department <span className="text-danger fs-14">*</span></label>
                      <input type="text" required name="department" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Work Phone Number <span className="text-danger fs-14">*</span></label>
                      <input type="text" pattern="[0-9]*" minlength="10" onChange={handleInputChange} required  name="phone_number" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Mobile Phone Number <span className="text-danger fs-14">*</span></label>
                      <input type="text" pattern="[0-9]*" minlength="10" onChange={handleInputChange} required name="mobile_number" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Home Phone Number <span className="text-danger fs-14">*</span></label>
                      <input type="text" pattern="[0-9]*" minlength="10" onChange={handleInputChange} required name="home_phone_number" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Fax Number <span className="text-danger fs-14">*</span></label>
                      <input type="text" required name="fax_number" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12 col-lg-6">
                    <div className="d-flex flex-column gap-2">
                      <label for="">Email-Id <span className="text-danger fs-14">*</span></label>
                      <input type="email" required name="email" placeholder="type here" />
                    </div>
                  </div>
                  <div className="col-12">
                    <div className="d-flex align-items-center gap-3 justify-content-end">

                      <button className="sub-btn" type="submit">Submit</button>
                    </div>
                   
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
    <AdminFooter />
    </div>
    
    </>
  );
    
  
}