import React, { useState,useEffect } from "react";
import axios from "axios";
import { Alert} from 'antd';
import { useNavigate } from "react-router-dom";
import TextArea from "antd/es/input/TextArea";
export const Register = () => {
  const navigate = useNavigate();
  useEffect(() => {
    const userStr = JSON.parse(localStorage.getItem("users"));
    document.title = 'Register Page';
    if (userStr) {
        navigate('/home');
    }
}, []);
  const [values, setValues] = useState({
    name: "",
    email: "",
    password: "",
    contact: "",
    address: "",
  });
  const [showAlert, setShowAlert] = useState(false)
  const [showPass, setShowPass] = useState(false)
  const [val, setVal] = useState("");
  const handleInput = (event) => {
    setVal((v) => (event.target.validity.valid ? event.target.value : v))
    setValues(prev => ({...prev, [event.target.name]:[event.target.value]})

    )}
  const handleSubmit = (event) => {
    event.preventDefault();
    var vll = event.target;
    let formdata = {
      first_name: vll.first_name.value,
      middle_name: vll.middle_name.value,
      last_name: vll.last_name.value,
      email: vll.email.value,
      password: vll.password.value,
      contact: vll.contact.value,
      address: vll.address.value,
      skills:vll.skills.value,
      years: vll.years.value
    };
    if(vll.password.value != vll.confirm_password.value){
      setShowPass(true);
      setShowPass({ type: 'error' });
    } else {
    //console.log(formdata);
      axios.post("http://localhost:8000/register", formdata,{headers:{'Access-Control-Allow-Origin':'*','Access-Control-Allow-Methods': 'POST,PUT','Accept': 'application/json','Content-Type':'application/json'}})
      .then(res => { 
        console.log(res.data);
        if(res.data.status == '2'){
          setShowAlert(true);
          setShowAlert({ type: 'error' });
          
        } else {
          setShowAlert(true);
          setShowAlert({ type: 'success' });
          event.target.reset();
          (event.target).text()
        }
        setTimeout(() => {
          setShowAlert(false);
        }, 4500);
      }) 
      .catch(err => { 
        //console.log(err.response.data);
        
        
      })
    }
  };
  return (
    <>
    <header>
      <div className="container-lg">
        <div className="row gy-4 align-items-center justify-content-between">
          <div className="col-3 col-md-2">
            <a href="#" className="logo-top">
              <img src={require('./assets/images/logo1.png')} alt="logo"/>
            </a>
          </div>
          <div className="col-md-2 d-none d-md-block">
            <a href="#" className="logo-top">
              <img src={require('./assets/images/logo2.png')} alt="logo"/>
            </a>
          </div>
          <div className="col-9 col-md-4">
            <div className="navbar-list d-flex gap-3 justify-content-end">
            <a href="/login" >Login</a>
              <a href="/register" className="active">Register</a>
            </div>
          </div>
        </div>
      </div>
    </header>
    <div className="fullscreen d-flex justify-content-center align-items-center">
      <div className="d-flex flex-column gap-3">
        <div
          className="reg-box d-flex flex-column gap-3 px-3 px-md-5 pt-40 pb-60 w-300 w-md-600"
        >
          <div className="d-flex justify-content-center align-items-center">
            <div className="flex flex-column gap-2">
              <h3 className="fs-30 fw-bold text-center mytext-primary">Register</h3>
            </div>
          </div>
          <div className="row justify-content-center">
            <div className="col-md-8">
            {
            showAlert?.type === 'error'&&<Alert type="error"  description="Email is already exist" closable/>
            }
            {
              showAlert?.type === 'success'&&<Alert type="success"  description="Register successfully" closable/>
            }
          <form action="" onSubmit={handleSubmit} method="post">
          
            <div className="d-flex flex-column gap-3">
              <div className="mt-2">
                <label className="mb-2 text-white">First Name <span className="text-danger fs-16">*</span></label>
                <div className="form-box rel">
                  <i className="fal fa-user top-icon"></i>
                  <input type="text" onChange={handleInput} name="first_name" required placeholder="First Name" />
                </div>
              </div>
              <div className="mt-2">
                <label className="mb-2 text-white">Middle Name(Optional)</label>
                <div className="form-box rel">
                  <i className="fal fa-user top-icon"></i>
                  <input
                    type="text"
                    name="middle_name"
                    onChange={handleInput}
                    placeholder="Middle Name"
                  />
                </div>
              </div>
              <div className="mt-2">
                <label className="mb-2  text-white">Last Name <span className="text-danger fs-16">*</span></label>
                <div className="form-box rel">
                  <i className="fal fa-user top-icon"></i>
                  <input type="text" onChange={handleInput} name="last_name" required placeholder="Last Name" />
                </div>
              </div>
              <div className="mt-2">
                <label className="mb-2  text-white">Phone Number <span className="text-danger fs-16">*</span></label>
                <div className="form-box rel">
                  <i className="fal fa-phone-alt top-icon"></i>
                  <input
                    type="text"
                    name="contact"
                    onChange={handleInput}
                    required
                    placeholder="Phone Number"
                  />
                </div>
              </div>


              <div className="mt-2">
                <label className="mb-2  text-white">Email-Id <span className="text-danger fs-16">*</span></label>
                <div className="form-box rel">
                  <i className="fal fa-envelope top-icon"></i>
                  <input
                    type="email"
                    name="email"
                    onChange={handleInput}
                    required
                    placeholder="Email Address"
                  />
                </div>
              </div>  
              <div className="mt-2">
                <label className="mb-2  text-white">Password <span className="text-danger fs-16">*</span></label>            
                <div className="form-box rel">
                  <i className="fal fa-lock top-icon"></i>
                  <input
                    type="password"
                    name="password"
                    onChange={handleInput}
                    required
                    placeholder="Password"
                  />
                </div>
              </div>
              
              <div className="mt-2">
                <label className="mb-2  text-white">Re-Enter Password <span className="text-danger fs-16">*</span></label>            
                <div className="form-box rel">
                  <i className="fal fa-lock top-icon"></i>
                  <input
                    type="password"
                    name="confirm_password"
                    onChange={handleInput}
                    required
                    placeholder="Re-Enter Password"
                  />
                  <div className="mt-2">
                  {
                    showPass?.type === 'error'&&<Alert type="error"  description="Password does not match" closable/>
                  }
                  </div>
                </div>
              </div>
              <div className="mt-2">
                <label className="mb-2  text-white">Address <span className="text-danger fs-16">*</span></label> 
                <div className="form-box rel">
                  <i className="fal fa-location top-icon"></i>
                  <input
                    type="text"
                    name="address"
                    onChange={handleInput}
                    required
                    placeholder="Address"
                  />
                </div>
              </div>
              <h5 class="mt-2  text-white">Skill and Extra Informations</h5>
              <div className="mt-2">
                <label className="mb-2  text-white">Mention All Skills <span className="text-danger fs-16">*</span></label> 
                <div className="form-box rel">
                  <TextArea
                    name="skills"
                    onChange={handleInput}
                    required
                    placeholder="Skills"
                  />
                </div>
              </div>
              <div className="mt-2">
                <label className="mb-2  text-white">Years of Experience <span className="text-danger fs-16">*</span></label> 
                <div className="form-box rel">
                  <input
                    type="number"
                    pattern="[0-9]*"
                    name="years"
                    onChange={handleInput}
                    
                    required
                  />
                </div>
              </div>
              <div>
                <button type="submit" className="submit-btn">
                  Submit
                </button>
              </div>
            </div>
          </form>
          {
            showAlert?.type === 'error'&&<Alert type="error" description="Email is already exist" closable/>
          }
          {
            showAlert?.type === 'success'&&<Alert type="success"  description="Register successfully" closable/>
          }
          <div className="d-flex justify-content-center align-items-center my-3">
            <div className="flex flex-column gap-4 w-md-300">
              <h3 className="fs-18 fw-bold text-center mytext-primary">
                <a href="/login" className="mytext-primary">
                   Login
                </a>
              </h3>
            </div>
          </div>
            </div>
           
          </div>
    
          
        </div>
      </div>
    </div>
    </>
  );
};
