import React, { useState,useEffect } from "react";
import axios from "axios";
import {message, Form, Input, Button, Alert} from 'antd';
import {Link, useNavigate } from "react-router-dom";
import TextArea from "antd/es/input/TextArea";
import AdminHeader from './AdminHeader';
import AdminFooter from './AdminFooter';
export const AdminEmployee = () => {
  const navigate = useNavigate();
  const [emData,setData] = useState([]);
  const [isClicked, setIsClicked] = useState(false);
  const [gdata, getData] = useState('');
  const handlegetClient = (userId) => {
    let formdata = {
      userId: userId,
    };
    axios.post("http://localhost:8000/admin/getuser", formdata,{headers:{'Access-Control-Allow-Origin':'*','Access-Control-Allow-Methods': 'POST,PUT','Accept': 'application/json','Content-Type':'application/json'}})
      .then(res => { 
       
        getData(res.data.row[0]);
        setIsClicked(true);
        
      }) 
      .catch(err => { 
        //console.log(err.response.data);
        
        
      })
  };
  useEffect(() => {
    document.title = 'Employee Page';
    const userStr = JSON.parse(localStorage.getItem("admin"));
    console.log(userStr);
    if (userStr == null) {
        navigate('/admin/login');
    }
    axios.get("http://localhost:8000/admin/getemployee",{headers:{'Access-Control-Allow-Origin':'*','Access-Control-Allow-Methods': 'POST,PUT','Accept': 'application/json','Content-Type':'application/json'}})
    .then(res => { 
      console.log(res.data.results);
      setData(res.data.results);
      
    }) 
    .catch(err => { 
      
    })
    
  
    
}, []);





  
  return (
    <>
    <div>
    <AdminHeader />
    <div className="main-right-body">
      <div className="block-full">
      <div className="row">
        <div className="float-end d-flex justify-content-end">
          <Link to ="/admin/addemployee" className="addclient">Add Employee</Link>
          </div>
        </div>
        <div className="row gy-4">
          <div className="col-md-4">
            <div className="d-block red-heading bg-white shadow p-2">
              <h3 className="mb-3">Select Employee</h3>
              <div className="over-empl">
                <div className="d-flex flex-column h-100">
                {Array.isArray(emData) ? (
                  emData.map((item, index) => <Link to="javascript:void(0)" onClick={() => handlegetClient(item.id)} >{item.first_name} {item.last_name}</Link>)
                ) : (
                  <Link> No data available </Link>
                )}
                </div>
              </div>
            </div>
          </div>
          {isClicked && 
          <div className="col-md-8">
            <div className="right-form-box bg-white shadow p-2">
              <div className="row gap-2">
                <div className="col-12 col-lg-6">
                  <div className="d-flex justify-content-between p-2">
                    <label for="">Employee Name:-</label>
                    <span className="fs-14">{gdata.first_name} {gdata.last_name}</span>
                  </div>
                  <div className="d-flex justify-content-between p-2">
                    <label for="">Phone Number:-</label>
                    <span className="fs-14">{gdata.contact}</span>
                  </div>
                  <div className="d-flex justify-content-between p-2">
                    <label for="">Email-Id:-</label>
                    <span className="fs-14">{gdata.email}</span>
                  </div>
                  <div className="d-flex justify-content-between p-2">
                    <label for="">Address:-</label>
                    <span className="fs-14">{gdata.address}</span>
                  </div>
                  <div className="d-flex justify-content-between p-2">
                    <label for="">Skills:-</label>
                    <span className="fs-14">{gdata.skills}</span>
                  </div>
                  <div className="d-flex justify-content-between p-2">
                    <label for="">Years Of Experience:-</label>
                    <span className="fs-14">{gdata.years}</span>
                  </div>
                 
                </div>
              </div>
              
            </div>
          </div>
          }
        </div>
      </div>
    </div>
    <AdminFooter />
    </div>
    
    </>
  );
    
  
}